const checkJsApi = () => {

  return new Promise((resolve, reject) => {
    window.wx.checkJsApi({
      jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"], // JSJS2,
      success: function (res) {
        // apitruefalse
        // {"checkResult":{"chooseImage":true},"errMsg":"checkJsApi:ok"}
        for (let key in res.checkResult) {
          if (!res.checkResult[key]) {
            reject("")
          }
        }
        console.log('checkJsApiOK');
        resolve('ok')
      },
      fail: function (res) {
        console.log('checkJsApifail');
        reject(res)
      }
    })
  })
}

/*  */
export default function wxShare(wxConfig, shareData, success, error) {
  // console.log('wxConfig===>',wxConfig)
  return new Promise((resolve, reject) => {
    checkJsApi().then(res => {
      window.wx.config({
        debug: false, // ,apialertpclogpc
        appId: wxConfig.AppId, // corpid
        timestamp: wxConfig.Timestamp, // 
        nonceStr: wxConfig.NonceStr, // 
        signature: wxConfig.Signature,// 1
        jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage"] // JSJS2
      });
      /*
   * 
   * icon,
   * */
      window.wx.ready(function () {
        resolve({
          status: 'ok',
          code: 200
        })
        /**/
        wx.onMenuShareTimeline({
          title: shareData.title, // 
          link: shareData.link, // 
          imgUrl: shareData.imgUrl, // 
          success: function () {
            // 
            console.log('')
            if(success && typeof success === 'function'){
              success()
            }
            
          },
          cancel: function () {
            // 
            console.log('')
            if(error && typeof error === 'function'){
              error()
            }
            
          }
        });

        /**/
        window.wx.onMenuShareAppMessage({
          title: shareData.title, // 
          desc: shareData.desc, // 
          link: shareData.link, // 
          imgUrl: shareData.imgUrl, // 
          type: '', // ,musicvideolinklink
          dataUrl: '', // typemusicvideo
          success: function () {
            // 
            console.log('')
            if(success && typeof success === 'function'){
              success()
            }
          },
          cancel: function () {
            // 
            console.log('')
            if(error && typeof error === 'function'){
              error()
            }
          }
        });
      })
      window.wx.error(function (res) {
        // configerrorconfigdebugresSPA
        reject(res)
      });
    }).catch((err) => {
      reject(err)
    })
  })
}



